.gpt3__cta {
  color: #fff;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 2rem;
  margin: 4rem;
  border-radius: 1rem;
  background: var(--gradient-bar);
}

.gpt3__cta-content {
}

.gpt3__cta-content__text {
  font-family: var(--font-family);
  font-weight: 500;
  font-size: 12px;
  line-height: 30px;
  color: #0e0e0e;
}

.gpt3__cta-content__title {
  font-family: var(--font-family);
  font-weight: 800;
  font-size: 24px;
  line-height: 45px;
  color: #000000;
}

.gpt3__cta-button {
  display: flex;
  align-items: center;
  justify-content: center;
  margin-left: 2rem;
}

.gpt3__cta-button button {
  padding: 0.5rem 1rem;
  background: #000000;
  outline: none;
  border: none;
  border-radius: 40px;
  font-family: var(--font-family);
  font-weight: 700;
  font-size: 18px;
  line-height: 30px;
  color: #ffffff;
  cursor: pointer;
  transition: all 0.3s ease-in-out;
  max-height: 64px;
  min-width: 150px;
  cursor: pointer;
}

.gpt3__cta-button button:hover {
  background: #141414;
}

@media screen and (max-width: 750px) {
  .gpt3__cta {
    flex-direction: column;
    margin: 2rem;
  }
  .gpt3__cta-button {
    margin: 1.5rem 0;
    display: flex;
  }
  .gpt3__cta-content__title {
    text-align: center;
    width: 100%;
    font-size: 20px;
    line-height: 24px;
  }
  .gpt3__cta-content__text {
    text-align: center;
    margin-bottom: 0.5rem;
    line-height: 20px;
  }
}

@media screen and (max-width: 550px) {
  .gpt3__cta-content__title {
    font-size: 18px;
  }
   .gpt3__cta {
    padding: 1rem;
  }
  .gpt3__cta-button button {
    font-size: 18px;
  }
}
