.gpt3__blog-conatiner_article {
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  background: var(--color-footer);
}

.gpt3__blog-container_article-img {
  width: 100%;
  height: 100%;
  background: var(--color-footer);
}

.gpt3__blog-container_article-img img {
  width: 100%;
  height: 100%;
  opacity: 0.8;
  transition: all 0.3s ease-in-out;
}

.gpt3__blog-container_article-img img:hover {
  opacity: 1;
}

.gpt3__blog-container_article-content {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  padding: 1rem 1.5rem;
  height: 100%;
  font-family: var(--font-family);
  color: #ffffff;
}

.gpt3__blog-container_article-date {
  font-weight: 700;
  font-size: 11.649px;
  line-height: 35px;
}

.gpt3__blog-container_article-title {
  font-weight: 800;
  font-size: 19.3154px;
  line-height: 30px;
  margin-bottom: 5rem;
}

.gpt3__blog-container_article-link {
  font-weight: 700;
  font-size: 11.649px;
  line-height: 35px;
  cursor: pointer;
}

@media screen and (max-width: 550px) {
  .gpt3__blog-container_article-title {
    font-size: 14px;
    line-height: 20px;
    margin-bottom: 1rem;
  }
  .gpt3__blog-container_article-content {
    padding: 0.5rem;
   }
}