.gpt3__features-container__feature {
  width: 100%;
  flex: 30%;
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
  flex-direction: column;
}


.gpt3__features-container__feature-content {
  min-width: 180px;
  flex: 1;
  margin-right: 2rem;
}

.gpt3__features-container__feature-title {
  font-family: var(--font-family);
  font-weight: 800;
  font-size: 18px;
  line-height: 24px;
  letter-spacing: -0.04em;
  color: #ffffff;
  margin-bottom: 0.3rem;
}

.gpt3__features-container__feature-line {
  width: 38px;
  height: 3px;
  background: var(--gradient-bar);
  box-shadow: 0 4px 4px rgba(0, 0, 0, 0.25);
  margin-bottom: 0.24rem;
}

.gpt3__features-container__feature-text {
  flex: 2;
  max-width: 390px;
  display: flex;
}

.gpt3__features-container__feature-text p {
  font-family: var(--font-family);
  font-weight: 500;
  font-size: 14px;
  line-height: 24px;
  color: var(--color-text);
}

@media screen and (max-width: 550px) {
  .gpt3__features-container__feature-title {
    font-size: 14px;
    line-height: 22px;
  }

  .gpt3__features-container_feature-text p {
    font-size: 12px;
    line-height: 20px;
  }
}
