.gpt3__navbar {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 2rem 6rem;
}

.gpt3__navbar-links {
  flex: 1;
  display: flex;
  justify-content: flex-start;
  align-items: center;
}

.menu__link {
  position: relative;
}

.menu__link::after,
.menu__link::before {
  content: "";
  position: absolute;
  bottom: -7px;
  width: 0px;
  height: 3px;
  background: #ff4820;
  transition: all 0.3s ease-in-out;
}

.menu__link::after {
  left: 50%;
}

.menu__link::before {
  right: 50%;
}

.menu__link:hover::before,
.menu__link:hover::after {
  width: 50%;
}

.gpt3__navbar-links_logo {
  margin-right: 2rem;
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;
}

.gpt3__navbar-links_logo img {
  width: 62px;
  height: 16px;
  margin-top: 10px;
}

.gpt3__navbar-links_container {
  list-style: none;
  display: flex;
}

.gpt3__navbar-links_container a,
.gpt3__navbar-sign a,
.gpt3__navbar-menu_container a {
  color: #ffff;
  font-family: var(--font-family);
  font-weight: 500;
  font-size: 18px;
  line-height: 25px;
  text-transform: capitalize;
  margin: 0 1rem;
}

.gpt3__navbar-sign {
  display: flex;
  justify-content: flex-end;
  align-items: center;
}

.gpt3__navbar-sign_btn {
  padding: 1rem 2rem;
  color: #fff;
  background: #ff4820;
  border-radius: 5px;
  border: transparent;
  font-family: var(--font-family);
  font-size: 18px;
  line-height: 25px;
  cursor: pointer;
  outline: none;
}

.gpt3__navbar-sign_btn:hover {
  background: #ce3411;
}

.gpt3__navbar-menu {
  margin-left: 1rem;
  display: none;
  position: relative;
}

.gpt3__navbar-menu svg {
  cursor: pointer;
}

.gpt3__navbar-menu_container {
  display: flex;
  justify-content: flex-start;
  align-items: center;
  text-align: center;
  flex-direction: column;
  text-align: end;
  background: var(--color-footer);
  padding: 2rem;
  position: absolute;
  z-index: 2;
  height: 80vh;
  top: 40px;
  right: 0;
  margin-top: 1rem;
  min-width: 300px;
  border-radius: 5px;
  box-shadow: 0 0 5px rgba(0, 0, 0, 0.2);
}

.gpt3__navbar-menu_container a {
  margin: 1rem 0;
}

.gpt3__navbar-menu_container-links {
  list-style: none;
  display: flex;
  flex-direction: column;
}

.gpt3__navbar-menu_container-links li {
  margin: 0.5rem 0;
}

.gpt3__navbar-menu_container-links-sign {
  display: none;
}

@media screen and (max-width: 1071px) {
  .gpt3__navbar-links_container {
    display: none;
  }
  .gpt3__navbar-menu {
    display: flex;
  }
  .menu__link::after,
  .menu__link::before {
    display: none;
  }
}

@media screen and (max-width: 700px) {
  .gpt3__navbar {
    padding: 2rem 4rem;
  }
}

@media screen and (max-width: 550px) {
  .gpt3__navbar {
    padding: 2rem;
  }
  .gpt3__navbar-sign {
    display: none;
  }
  .gpt3__navbar-menu_container {
    top: 20px;
    width: calc(100vw - 2.5rem);
    height: 85vh;
  }

  .gpt3__navbar-menu_container-links {
    margin-bottom: 2rem;
  }

  .gpt3__navbar-menu_container-links-sign {
    display: flex;
    flex-direction: column;
    text-align: center;
  }
  .gpt3__navbar-menu_container-links li {
    text-align: center;
  }
}

@media screen and (max-width: 350px) {
  .gpt3__navbar-menu_container {
    min-width: 260px;
  }
}
