.gpt3__possibility {
  display: flex;
}

.gpt3__posssibility-img {
  flex: 1;
  display: flex;
  justify-content: flex-start;
  align-items: center;

  margin-right: 2rem;
}

.gpt3__posssibility-img img {
  width: 100%;
  height: 100%;
  transition: all 0.3s ease-in-out;
  opacity: 0.7;
}

.gpt3__posssibility-img img:hover {
  transform: translateY(-5px);
  opacity: 1;
}

.gpt3__possibility-content {
  flex: 1;
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
  align-items: flex-start;
}

.gpt3__possibility-content__title {
  font-family: var(--font-family);
  font-weight: 800;
  font-size: 34px;
  line-height: 45px;
  margin: 1rem 0;
}

.gpt3__possibility-content__subtitle {
  font-family: var(--font-family);
  font-weight: 500;
  font-size: 16px;
  line-height: 30px;
}

.subtitle__blue {
  color: #71e5ff;
}

.subtitle__orange {
  color: #ff8a71;
}

.gpt3__possibility-content__text {
  font-family: var(--font-family);
  color: var(--color-text);
  font-weight: 400;
  font-size: 16px;
  line-height: 30px;
  margin-bottom: 2rem;
}

@media screen and (max-width: 950px) {
  .gpt3__possibility {
    flex-direction: column;
  }
  .gpt3__posssibility-img {
    margin: 1rem 0;
  }
  .gpt3__possibility-content {
    margin-top: 2rem;
  }
}
