.gpt3__footer {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-between;
  background: var(--color-footer);
  color: #ffff;
}

.gpt3__footer-heading {
  width: 100%;
  text-align: center;
  margin-bottom: 3rem;
}

.gpt3__footer-heading-title {
  font-family: var(--font-family);
  color: #ffffff;
  font-weight: 800;
  font-size: 62px;
  line-height: 75px;
  text-align: center;
  letter-spacing: -0.04em;
}

.gpt3__footer-heading-btn {
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 1rem;
  border: 1px solid #ffffff;
  margin-bottom: 4rem;
}

.gpt3__footer-heading-btn button {
  word-spacing: 2px;
  font-family: var(--font-family);
  font-size: 18px;
  line-height: 21px;
  color: #fff;
  background-color: transparent;
  outline: none;
  border: none;
  cursor: pointer;
}

.gpt3__footer-links_logo {
  display: flex;
  flex-direction: column;
}

.gpt3__footer-links_logo-img {
  width: 118px;
  height: 30px;
  margin-bottom: 1rem;
}

.gpt3__footer-links {
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
  flex-wrap: wrap;
  width: 100%;
  text-align: left;
  gap: 20px;
  margin-bottom: 4rem;
}

.gpt3__footer-links_block {
  width: 250px;
  margin: 0 1rem;
}

.gpt3__footer-links_block-list {
  list-style: none;
}

.gpt3__footer-links_block-list li {
  margin-bottom: 1rem;
}

.gpt3__footer-links_block-title {
  font-family: var(--font-family);
  font-size: 14px;
  line-height: 16px;
  color: #ffffff;
  margin-bottom: 1.5rem;
}

.gpt3__footer-bottom {
  font-family: var(--font-family);
  font-size: 12px;
  line-height: 14px;
  color: #ffffff;
}

@media screen and (max-width: 850px) {
  .gpt3__footer-heading-title {
    font-size: 44px;
    line-height: 50px;
  }
}

@media screen and (max-width: 770px) {
  .gpt3__footer-links_logo {
    align-items: center; 
  }
  .gpt3__footer-links {
    justify-content: center;
    text-align: center;
  }
}

@media screen and (max-width: 550px) {
  .gpt3__footer-heading-title {
    font-size: 28px;
    line-height: 38px;
  }
}

@media screen and (max-width: 450px) {
  .gpt3__footer-links_block-title {
    text-decoration: underline;
  }
  .gpt3__footer-heading {
    margin-bottom: 2rem;
  }
}

