.gpt3__header {
  display: flex;
}

.gpt3__header-content {
  flex: 1;
  display: flex;
  justify-content: center;
  align-items: flex-start;
  flex-direction: column;
  margin-right: 5rem;
}

.gpt3__header-content__title {
  font-family: var(--font-family);
  font-weight: 800;
  font-size: 62px;
  line-height: 75px;
  letter-spacing: -0.04em;
  margin-bottom: 2rem;
}

.gpt3__header-content__text {
  font-family: var(--font-family);
  font-weight: 400;
  font-size: 20px;
  line-height: 27px;
  color: #81afdd;
}

.gpt3__header-content__input {
  width: 100%;
  margin: 2rem 0 1rem;
  display: flex;
}

.gpt3__header-content__input input {
  width: 100%;
  flex: 2;
  outline: none;
  min-height: 50px;
  padding: 0 1rem;
  font-family: var(--font-family);
  font-weight: 400;
  font-size: 20px;
  line-height: 27px;
  color: #fff;
  background: var(--color-footer);
  border: 1px solid var(--color-footer);
  border-radius: 5px 0px 0px 5px;
}

.gpt3__header-content__input-btn {
  flex: 0.6;
  width: 100%;
  min-height: 50px;
  padding: 0 1rem;
  background: #ff4820;
  border-radius: 0px 5px 5px 0px;
  font-family: var(--font-family);
  font-weight: 700;
  font-size: 20px;
  line-height: 27px;
  color: #ffffff;
  cursor: pointer;
  outline: none;
  border: 1px solid transparent;
}

.gpt3__header-content__input-btn:hover {
  background: #ce3411;
}

.gpt3__header-content__people {
  width: 100%;
  display: flex;
  justify-content: flex-start;
  align-items: center;
  margin-top: 1rem;
}

.gpt3__header-content__people img {
  width: 182px;
  height: 38px;
  margin-right: 1rem;
}

.gpt3__header-image img {
  transition: all 0.3s ease-in-out;
  opacity: 0.7;
}
.gpt3__header-image img:hover {
  transform: translateY(-5px);
  opacity: 1;
}

.gpt3__header-content__people-text {
  font-family: var(--font-family);
  font-weight: 500;
  font-size: 12px;
  line-height: 38px;
  color: #ffffff;
}

.gpt3__header-image {
  flex: 1;
  display: flex;
  justify-content: center;
  align-items: center;
}

.gpt3__header-image img {
  width: 100%;
  height: 100%;
}

@media screen and (max-width: 1050px) {
  .gpt3__header {
    flex-direction: column;
  }
  .gpt3__header-content {
    margin: 0 0 3rem;
  }
}

@media screen and (max-width: 650px) {
  .gpt3__header-content__title {
    font-size: 48px;
    line-height: 60px;
    margin-bottom: 1rem;
  }

  .gpt3__header-content__people {
    flex-direction: column;
  }

  .gpt3__header-content__people-text {
    margin: 0;
  }
  .gpt3__header-content__input input,
  .gpt3__header-content__input-btn,
  .gpt3__header-content__text {
    font-size: 16px;
    line-height: 24px;
  }
}

@media screen and (max-width: 490px) {
  .gpt3__header-content__title {
    font-size: 36px;
    line-height: 48px;
    margin-bottom: 0.5rem;
  }

  .gpt3__header-content__text {
    font-size: 14px;
  }

  .gpt3__header-content__input input,
  .gpt3__header-content__input-btnt {
    font-size: 12px;
    line-height: 16px;
  }
  .gpt3__header-content__people-text {
    text-align: center;
    line-height: 16px;
    margin-top: 0.5rem;
  }
}
